#results {
  font-size: 48px;
  font-weight: 600;
  transition: all .3s;
}

#quiz-name {
  margin-left: 2rem;
  margin-right: 2rem;
}

#correct-answer-text, #your-answer-text {
  margin: 2rem 3rem 0 2rem;
}

#correction-contianer {
  width: 100%;
  flex-direction: column;
  display: flex;
}

#btn-flex {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#check-btn {
  background-color: #40c057;
  justify-self: center;
}

#next-btn {
  background-color: #2e67d1;
}

#theme {
  margin-top: 2rem;
}

#random-range-flex {
  justify-content: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
  display: flex;
}

.answer-input-box {
  width: 80%;
  height: 5rem;
  text-align: start;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-column: 2;
  margin-top: 5rem;
  padding: 1.2rem;
  font-family: inherit;
  font-size: 1.3rem;
  position: relative;
}

.quiz-answer-text {
  width: 80rem;
  margin: 2rem 3rem 0 0;
  font-size: 18px;
  font-weight: 600;
  overflow: hidden;
}

#through-symbol {
  margin-left: 2rem;
  font-size: 16px;
  font-weight: 600;
}

#r-max {
  margin-right: 2rem;
}

#theme-settings-container, #quiz-container {
  width: 100%;
  height: 100%;
}

#settings-container {
  width: 40%;
  height: auto;
  background-color: #868e96;
  border-radius: 15px;
  margin-top: 2rem;
  margin-bottom: 8rem;
}

#multiple-choice-wrap {
  width: 80vw;
  justify-self: center;
  margin-top: 4rem;
}

#random-question-range {
  margin-left: 2rem;
  font-size: 16px;
  font-weight: 600;
}

.quiz-btn {
  width: 15rem;
  color: #fff;
  border: none;
  border-radius: 10px;
  margin: 2rem 3rem 1rem;
  padding: .8rem;
  font-size: 1.8rem;
  font-weight: 450;
  text-decoration: none;
  transition: all .2s;
}

.random-range-input-box {
  height: 2rem;
  width: 5rem;
  text-align: center;
  border: .1rem solid #00000080;
  border-radius: .9rem;
  grid-area: 2 / 2 / 3;
  margin-left: 2rem;
  padding: .5rem;
  font-family: inherit;
  font-size: 1.3rem;
}

.quiz-sub-text {
  font-size: 24px;
  font-weight: 600;
}

.choice {
  margin: 1rem;
}

.quiz-question {
  border-bottom: .1rem solid #0000003d;
  margin: 5rem;
  padding: 0 40% 2rem;
}

.quiz-header {
  width: 100%;
  grid-template-columns: repeat(3, 1fr);
  align-self: center;
  margin-top: 5rem;
  display: grid;
}

.quiz-title {
  grid-area: 1 / 2 / auto / 3;
  place-self: center;
  font-size: 32px;
  font-weight: 600;
}

.combo-text {
  grid-area: 1 / 1;
  place-self: center;
}

.highest-combo-text {
  grid-area: 1 / 3;
  place-self: center;
}

.custom-radio-btn {
  width: 25px;
  height: 25px;
  border: 2px solid #ffb300;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-left: .1rem;
  margin-right: .1rem;
  display: flex;
}

.custom-radio-btn .selected {
  width: calc(100% - 6px);
  height: calc(100% - 6px);
  opacity: 0;
  background-color: #000;
  border-radius: 50%;
  transition: opacity .1s;
  display: inline-block;
}

.custom-radio-btn input {
  display: none;
}

.custom-radio-btn input:checked + .selected {
  opacity: 1;
}

.mc-answer-flex {
  width: 100%;
  justify-content: start;
  align-items: center;
  margin-bottom: 2rem;
  margin-right: 2rem;
  display: flex;
}

.mc-grid {
  grid-template-columns: repeat(3, auto);
  grid-auto-rows: auto;
  display: grid;
}

.quiz-mc-answer {
  width: 90%;
  margin-top: 1.5rem;
  margin-left: 1rem;
  font-size: 16px;
  font-weight: 600;
  overflow-x: scroll;
  overflow-y: hidden;
}

.theme-cs-grid button {
  width: 4rem;
  height: 4rem;
  border: none;
  border-radius: 50px;
  justify-self: center;
  box-shadow: 0 0 .4rem #0000002f;
}

.white-theme {
  background-color: #fff;
}

.gray-theme {
  background-color: #717171;
}

.purple-theme {
  background-color: #862e9c;
}

.theme-cs-grid {
  width: 60%;
  height: 5rem;
  border-radius: 15px;
  grid-area: 6 / 5;
  grid-template-columns: repeat(3, 1fr);
  align-self: center;
  align-items: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
  position: relative;
}

